export default {
    layout: "Emp-content",
    data() {
        return {
            designation: {
                type: "",
                designation: "",

            },
            errors: [],
        };
    },
    methods: {
        addDesignation() {

            this.$axios
                .$post("/designation/add", this.designation)
                .then((res) => {
                    this.designation = "";
                    this.errors = "";
                    this.$toaster.success(res.message);
                    this.$router.push("/employee/designation");
                })
                .catch((error) => {
                    this.errors = error.response.data.errors;
                    console.log(this.errors);
                });
        },
    },

};
